<template>
    <div id="process" class="ctrl-box">
     <!-- <img alt="Vue logo" src="./assets/V.png"> -->
    <!--<HelloWorld msg="Welcome to Your Vue.js App"/>-->
    <div class="side-left-box">
      <h1></h1>
      <div class="style-list-box"><!--  -->
          <a v-for="_style in styles "
            v-bind:key="_style.name"
            :class="{active:_style.name === style.name}"
             @click="setStyle(_style)"><!-- style-->
            <span class="name" v-text="_style.name"></span>
            <span class="cor vote"></span><!--  -->
           </a>
      </div>
    </div>
    <div class="side-right-box">
      <div class="btn-box"><!---->
        <a class="btn big wire" @click="chooseImage"></a>
        <button @click="output()" class="btn big"></button>
      </div>
      <parameterSetting :para=style @updateStyle="updateStyle"></parameterSetting>
    </div> 
    <div class="output">
			<img :src="src" v-if="src" :data-wide="style.wide">
	</div>
    <div class="img-list">
      <img v-for="src,index in images"
          v-bind:src="src"
          crossOrigin="Anonymous"
          @click="setImageAndDraw"
          @load="index===0&&setImageAndDraw($event)"><!--  -->
    </div>
    </div>
</template>

<script>
import {styles} from '../script/styles.js'//js
import {images} from '../script/images.js'//js
import {defaultStyle} from '../script/var.js'//js
import {process} from '../script/process.js'//
import {chooseImage} from '../script/event.js'//
import parameterSetting from '../components/parameter-setting.vue'
const data = {
    src:'',
    img:null,
    style:{//
        ...defaultStyle,//string
        ...styles[0]
    },
    styles,//
    images //
};

const deepCopy = o=>JSON.parse(JSON.stringify(o));
export default 
{
    name: 'process',
    data: function () {
        return data;
    },
    components:{
      parameterSetting,
    },
    methods: {
        setStyle(style){
          this.style = deepCopy({
            ...defaultStyle,//style
            ...style
          });//
          this.process();
        },
        output(){
          const a = document.createElement('a');
          a.href = this.src;
          a.download = `${+Date.now()}.jpg`;
          a.click();
        },
        chooseImage(){
          chooseImage(img=>{
              this.img = img;
              this.src = img.src;
              this.process();
          });
        },
        setImageAndDraw(e){
          let img = e.target;
          if(/!w300/.test(img.src)){
            img.onload = ()=>{
              this.img = e.target;
              this.process();
            };
            img.src = img.src.replace(/!w300$/,'!h480')
          }else{
            this.img = e.target;
            this.process();
          }
        },
        process(){
          process(this.img,this.style,src=>{
            this.src = src;
            console.log(this.style.ratio);
          });
        },
        updateStyle(para){
          this.style = para;
          this.process();
          
        }
    },
    
}

</script>
<style scoped>

</style>
